.order-modal-msg {
  position: sticky;
  top: 130px;
  margin-bottom: 94px;
}

.good-price-section {
  width: 372px;
  padding: 24px;
  padding-left: 22px;
  margin-top: 24px;
  font-size: 0.8888em;
  color: $clr8;
  border: 1px solid rgb(221, 221, 221);
  border-radius: 12px;
  font-family: cereal-regular;
  line-height: 20px;
  position: relative;

  .good-price-header {
    display: block;
    font-family: airbnb-cereal-medium;
    margin-bottom: 9px;
  }

  .good-price-content {
    max-width: 417px;
  }

  .price-tag-icon {
    position: absolute;
    top: 20px;
    right: 10px;

    img {
      width: 32px;
      height: 32px;
    }
  }
}

.stay-details-order-container {
  .order-modal {
    border: 1px solid $clr9;
    border-radius: 12px;
    padding: 24px;
    padding-bottom: 20px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 6px 16px;
    color: $clr8;
    background-color: $clr11;
    width: 372px;
    display: flex;
    flex-direction: column;

    .order-modal-header {
      display: flex;
      margin-bottom: 22px;
      align-content: end;
      justify-content: space-between;
      height: 27px;

      .stay-rate {
        display: flex;
        align-items: center;

        .stay-rate-reviews {
          font-size: 0.7778em;
          color: $clr15;
          font-family: $font1;
          padding-inline-start: 2px;
        }

        .stay-preview-star {
          margin-bottom: 2.5px;
          margin-right: 2px;

          .stay-preview-avg {
            font-size: 0.7778em;
            font-weight: 500;
            font-family: $font1;
          }

          img {
            width: 12px;
            height: 12px;
            position: relative;
          }
        }
      }

      .price-per-night {
        width: 25%;
        display: flex;
        justify-content: space-between;
        align-items: end;
        font-family: $font1;

        .price {
          font-weight: 800;
          font-size: 22px;
          font-family: inherit;
          align-self: center;
        }

        .word-night {
          font-size: 16px;
          font-weight: 400;
          padding-inline-start: 5px;
        }
      }
    }

    .no-charge-msg {
      text-align: center;
      font-size: 14px;
      font-family: cereal-regular;
      margin-top: 17px;
      line-height: 18px;
      font-weight: 400px;
      text-size-adjust: 100%;
      line-break: strict;
      color: $clr8;
    }

    .check-availability {
      height: 48px;
      font-size: 16px;
      font-family: $font1;
    }

    .price-summary {
      margin-top: 20px;
      font-size: 1rem;
      font-family: cereal-regular;
      font-weight: 400;
      margin-bottom: 0;

      .nigths-price-calc {
        text-decoration: underline;
      }

      .fees {
        padding-top: 10px;
        margin-bottom: 1px;
        text-decoration: underline;
      }

      .total-price {
        margin-top: 21px;
        padding-top: 21px;
        border-top: 1px solid rgb(221, 221, 221);
        font-size: 1rem;
        font-weight: 600;
        color: $clr18;
        font-family: cereal-bold;
      }

      .total-price,
      .nigths-price-calc,
      .fees {
        display: flex;
        justify-content: space-between;
      }
    }

    .reservation-details {
      display: flex;
      flex-direction: column;
      font-family: $font1;
      font-size: 16px;
    }

    .user-prefs {
      height: 227px;
      margin: 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      position: relative;

      .guests {
        width: 320px;
        position: relative;
        top: -4px;
        cursor: pointer;

        .guests-close {
          height: 56px;
          display: flex;
          flex-direction: column;
          padding-inline-start: 12px;
          // position: relative;

          .order-heading {
            padding-block-start: 11px;
            margin: 0;
          }

          .guests-count {
            padding: 0;
            position: relative;
            bottom: 2px;
            width: 100%;
            height: 100%;
            font-size: 0.94em;

            span {
              color: $clr8;
              margin: 0;
              padding: 0;
              background-color: #22222200;
              font-family: circular-regular;
              line-height: 0;
              width: 100%;

              span {
                width: 100%;
              }
            }
          }
        }
      }
    }

    .dates-guests-container {
      height: 112.5px;
      margin: 0;
      padding: 0;
      margin-bottom: 16px;
      border: 1px solid $clr16;
      width: 322px;
      border-radius: 8px;
      color: rgb(34, 34, 34) !important;
      font-family: Circular, -apple-system, BlinkMacSystemFont, Roboto,
        'Helvetica Neue', sans-serif !important;
      font-weight: 400 !important;
      font-size: 16px !important;
      line-height: 20px !important;

      .picker-btns {
        width: 100%;
        display: flex;
        top: 5px;

        .check-in-btn,
        .check-out-btn {
          overflow: hidden;
          width: 100%;
          min-height: 56px;
          background-color: rgba(0, 0, 0, 0);
          margin: 0;
          border-bottom: 1px solid rgb(176, 176, 176);
          padding: 10px 12px 12px 12px;
          display: flex;
          flex-direction: column;
          justify-content: space-between;

          span {
            font-size: 14px;
            font-family: cereal-regular;
            font-weight: 400;
            color: rgb(113, 113, 113);
          }
        }

        .check-out-btn {
          border-left: 1px solid rgb(176, 176, 176);
        }

        &.active {
          display: flex;
          position: absolute;
          background-color: white;
          transform: translateY(70%);
          z-index: 3;
          margin-top: 7px;
          background-color: $clr11;
          border: 2px solid #000;
          border-radius: 8px;
          z-index: 3;
          width: 95.5%;
          margin-left: 12px;
        }
      }
    }

    .order-heading {
      font-size: 10px;
      font-family: cereal-regular;
      font-weight: 800;
    }

    .date-picker-container {
      background-color: $clr11;
      box-shadow: rgba(0, 0, 0, 0.2) 0px 6px 20px;
      border-radius: 16px;
      position: absolute;
